.page-stat {
  padding: 4px;
}
.page-stat .stat-tabs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.page-stat .stat-tabs .stat-tab {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  height: 153px;
  color: #fff;
  margin-right: 10px;
}
.page-stat .stat-tabs .stat-tab:last-of-type {
  margin-right: 0;
}
.page-stat .stat-tabs .stat-tab.tab1 {
  background-color: #3cdb86;
}
.page-stat .stat-tabs .stat-tab.tab2 {
  background-color: #4380f4;
}
.page-stat .stat-tabs .stat-tab.tab3 {
  background-color: #073145;
}
.page-stat .stat-tabs .stat-tab.tab4 {
  background-color: #f66833;
}
.page-stat .stat-tabs .stat-tab .stat-title {
  font-size: 12px;
}
.page-stat .stat-tabs .stat-tab .stat-number {
  font-size: 30px;
}
.page-stat .stat-cards {
  display: flex;
  margin-bottom: 10px;
}
.page-stat .stat-cards:last-of-type {
  margin-bottom: 0;
}
.page-stat .stat-cards .card-l {
  flex: 1 1;
  margin-right: 10px;
}
.page-stat .stat-cards .card-r {
  width: 36%;
  max-width: 500px;
}
.page-stat .stat-cards .ant-card-extra .anticon-more {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.page-stat .stat-cards .card-body {
  height: 190px;
  overflow: auto;
}
.page-stat .stat-cards .card-body #c1,
.page-stat .stat-cards .card-body #c2,
.page-stat .stat-cards .card-body #c3 {
  height: 100%;
  overflow: hidden;
}
.page-stat .common-navs .common-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  float: left;
  width: 10%;
  height: 95px;
}
.page-stat .common-navs .common-nav .nav-name {
  font-size: 12px;
  margin-top: 8px;
  padding-bottom: 10px;
}
